// ABSTRACTS (переменные, миксины)
@import "abstracts/variables";

// BASE (базовые стили)
*,
*::before,
*::after {
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
  font-size: 16px;
  scroll-behavior: smooth;
}

html,
body {
  height: 100%;
  position: relative;
  overflow-x: hidden;
}

body {
  font-family: $font-primary;
  font-style: normal;
  font-weight: 400;
  color: $text-color;
  margin: 0;
}

a {
  display: inline-block;
  text-decoration: none;
}

button {
  text-decoration: none;
}

ul,
li {
  margin: 0;
  padding: 0;
  list-style: none;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0 auto;
}

.wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100%;
}

.content {
  flex: 1 0 auto;
}

.footer {
  flex: 0 0 auto;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 $container-padding;
}

// COMPONENTS (кнопки, карточки)
@import "components/booking";
@import "components/rating";
@import "components/countdown";

// LAYOUTS (секции)
.header {
  position: absolute; // ← ДОБАВИЛИ
  top: 0;
  left: 0;
  width: 100%;
  z-index: 100; // ← ДОБАВИЛИ (чтобы был поверх hero)
  background: transparent;
  // Прозрачный, видно фон hero
  &__top {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    border-bottom: 1px solid #fd0;
    align-items: center;
    position: relative;
    padding: 14px 0;
  }

  &__social {
    display: flex;
    justify-content: flex-start;
    align-items: center;
  }

  &__social-list {
    margin-right: 30px;
  }

  &__top-right {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    .header__nav {
      display: none;
    }
  }

  &__location {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    align-items: center;
  }

  &__registration {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    align-items: center;
    margin-left: 64px;
  }

  &__location-adress {
    font-size: 12px;
    letter-spacing: 0.18px;
    color: #fff;
    margin-left: 15px;
  }

  &__registration-login,
  &__registration-register {
    text-transform: uppercase;
    font-size: 12px;
    font-weight: 600;
    color: #fff;
    &:hover {
      color: #ffc001;
      transition: all 0.3s ease;
    }
  }

  &__devider {
    width: 1px;
    height: 20px;
    background-color: #fd0;
    margin: 0 12px;
  }

  &__bottom {
    max-height: 75px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding-top: 16px;
    position: relative;
    // z-index: 1;
  }

  &__nav-menu {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
  }

  &__nav-list {
    margin: 0 25px;
  }

  &__nav-list:nth-child(1) {
    margin-left: 0;
  }

  &__nav-list:nth-child(5) {
    margin-right: 0;
  }

  &__nav-link {
    text-transform: uppercase;
    font-size: 14px;
    color: #fff;
    &:hover {
      color: #ffc001;
      transition: all 0.3s ease;
    }
  }
}

.fa-brands,
.fa-location-dot {
  color: #fff;
  &:hover {
    color: #ffc001;
    transition: all 0.3s ease;
  }
}

.hero {
  position: relative;
  min-height: 100vh; // ← ДОБАВИЛИ (на весь экран)
  background-color: #000;
  padding-top: 375px;
  margin-bottom: 308px;
  // ← ДОБАВИЛИ (отступ для header ~высота header)
  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-image: url(../img/background-hero.jpg);
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    opacity: 0.7;
    z-index: 0; // Позади контента
  }

  &__tel {
    position: relative; // ← ДОБАВИЛИ (чтобы был поверх фона)
    z-index: 1; // ← ДОБАВИЛИ
    margin-bottom: 40px;
    font-size: 20px;
    font-weight: 700;
    letter-spacing: 0.3px;
    color: #ffc001;
  }

  &__title {
    position: relative; // ← ДОБАВИЛИ
    z-index: 1; // ← ДОБАВИЛИ
    margin-bottom: 30px;
    font-size: 48px;
    font-weight: 700;
    color: #fff;
  }

  &__text {
    position: relative; // ← ДОБАВИЛИ
    z-index: 1; // ← ДОБАВИЛИ
    margin: 0 0 50px 0;
    max-width: 590px;
    font-size: 16px;
    text-align: left;
    color: #fff;
  }

  &__btn-block {
    position: relative; // ← ДОБАВИЛИ
    z-index: 1; // ← ДОБАВИЛИ
    margin-bottom: 100px; // ← ДОБАВИЛИ (чтобы booking widget не налез)
  }

  &__btn {
    width: 171px;
    height: 45px;
    padding: 14px 20px;
    border-radius: 22.5px;
    border: solid 1px #fff;
    background-color: transparent;
    font-size: 14px;
    font-weight: 700;
    text-transform: uppercase;
    cursor: pointer;
    text-align: center;
    align-items: center;
    color: #ffc001;
    &:hover {
      background-color: #ffc001;
      color: #fff;
      border: solid 1px #ffc001;
      transition: all 0.3s ease;
    }
  }
}

.section-title {
  font-size: 30px;
  font-weight: 700;
  color: $text-color;
}

.section-text {
  font-size: 16px;
  color: $text-color;
}
.slick-initialized .slick-slide {
  display: flex;
  justify-content: center;
}

.slick-dots {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 16px;
}

.places {
  margin-bottom: 210px;
  &__subtitle {
    font-size: 14px;
    font-weight: 600;
    color: #ffc001;
    margin-bottom: 28px;
    text-transform: uppercase;
  }

  &__title {
    text-transform: uppercase;
    margin-bottom: 74px;
  }

  &__slider {
    margin-bottom: 166px;
    .slick-list {
      margin-bottom: 82px;
    }
    .slick-dots {
      display: flex;
      justify-content: center;
      align-items: center;
      height: 16px;
    }
  }

  &__title-2 {
    text-align: center;
    position: relative;
    margin-bottom: 39px;
    &::before {
      content: "";
      background-image: url(../img/populer-place.png);
      background-size: cover;
      background-position: center;
      background-repeat: no-repeat;
      position: absolute;
      width: 1169px;
      height: 111px;
      bottom: -30px;
      left: 0;
    }
  }

  &__text {
    text-align: center;
    max-width: 658px;
    width: 100%;
    margin-bottom: 79px;
  }

  &__slider-item {
    width: 370px;
    height: 370px;
    position: relative;
    cursor: pointer;
    &:hover {
      .slider__price {
        display: block;
      }

      .slider__info {
        height: 164px;
        padding-top: 50px;
        opacity: 0.9;
        background-color: #ffc001;
      }

      .slider__btn {
        display: block;
      }
    }
  }

  &__slider-img {
    width: 370px;
    height: 370px;
  }

  &__big-slider {
    .slick-list {
      margin-bottom: -71px;
    }
    .slick-arrow {
      font-size: 0;
      overflow: hidden;
      white-space: nowrap;
      color: transparent;
      position: absolute;
      bottom: calc(700px - 50%);
    }
    .slick-dots {
      position: relative;
      margin-top: 30px;
      z-index: 2;
    }

    /* убираем псевдо-элемент, который рисует надпись */
    .slick-arrow::before {
      content: none;
    }
    .slick-prev {
      border: none;
      background-color: transparent;
      left: 358px;
      // bottom: calc(740px - 50%);
      z-index: 10;
      width: 28px;
      height: 28px;
      &:before {
        content: "";
        display: block; // ← Критично! Без этого размер не работает
        width: 17px;
        height: 28px;
        background-image: url(../img/chevron-left-solid-full.svg);
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center;
        opacity: 1;
      }
    }

    .slick-next {
      border: none;
      background-color: transparent;
      right: 358px;
      // bottom: calc(740px - 50%);
      z-index: 10;
      width: 28px;
      height: 28px;
      &:before {
        content: "";
        display: block; // ← Критично! Без этого размер не работает
        width: 17px;
        height: 28px;
        background-image: url(../img/chevron-right-solid-full.svg);
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center;
        opacity: 1;
      }
    }

    .slick-dots {
      bottom: 23px;
    }
  }

  &__big-slider-item {
    position: relative;
  }
  &__big-slider-img-wrapper {
    // position: relative;
    padding-bottom: 110px;
  }
  .slick-list .draggable {
    margin-bottom: -71px;
  }
}

.slider {
  &__info {
    position: absolute;
    bottom: 0;
    z-index: 1;
    max-width: 370px;
    width: 100%;
    height: 94px;
    padding-top: 36px;
    opacity: 0.7;
    background-color: #f9f9f9;
  }

  &__price {
    font-size: 16px;
    font-weight: 600;
    text-align: center;
    color: $text-color;
    margin-bottom: 19px;
    display: none;
  }

  &__name {
    text-align: center;
    font-size: 20px;
    font-weight: 600;
    color: $text-color;
  }

  &__btn {
    position: absolute;
    bottom: -28px;
    right: 179px;
    width: 57px;
    height: 57px;
    background-color: #ffc001;
    border-radius: 50%;
    z-index: 2;
    display: none;
  }
}

.big-slider {
  &__img {
    max-width: 1920px;
    width: 100%;
  }

  &__plate {
    width: 470px;
    height: 205px;
    opacity: 0.75;
    border-radius: 5px;
    box-shadow: 0px 15px 30px 0 rgba(230, 230, 230, 0.3);
    background-color: #fff;
    position: absolute;
    left: calc(50% - 235px); // 50% минус половина ширины (470/2)
    transform: translateY(-57%);
    bottom: -100px;
    z-index: 10;
  }

  &__name {
    text-align: center;
    margin: 29px 0 24px 0;
    font-size: 24px;
    font-weight: 700;
    color: $text-color;
  }

  &__list {
    text-align: center;
    display: flex;
    justify-content: center;
  }

  &__item {
    width: 40px;
    height: 40px;
    padding: 11px 11px;
    border: solid 1px #fd0;
  }
}

.fa-eye,
.fa-heart,
.fa-comment,
.fa-share-nodes,
.fa-video {
  font-size: 16px;
  color: #fd0;
}

.fa-circle-arrow-down {
  width: 30px;
  height: 30px;
  font-size: 30px;
  color: #fff;
  margin: auto auto;
  display: block;
  margin-top: 13px;
}

.slick-dots li button {
  margin: 0 6px;
  display: block;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  cursor: pointer;
  outline: none;
  color: #fff;
  border: solid 1px #fd0;
  padding: 0; // убираем внутренние отступы slick-theme
  overflow: hidden; // прячем всё, что вылазит
  white-space: nowrap;
}

li.slick-active button {
  background-color: #fd0;
  width: 15px;
  height: 15px;
}

.honeymoon {
  padding-bottom: 50px;
  &__slider {
    margin: 0 auto;
    max-width: 1920px;
    padding: 0 5px;
    .slick-list {
      margin-bottom: 122px;
    }
  }

  &__title {
    text-align: center;
    margin-bottom: 39px;
  }

  &__text {
    text-align: center;
    max-width: 658px;
    width: 100%;
    margin-bottom: 79px;
  }

  &__slider-item {
    width: 620px;
    height: 357px;
    padding-top: 50px;
    display: flex;
    justify-content: space-between;
    box-shadow: 0 0 35px 0 rgba(186, 197, 198, 0.25);
  }

  &__slider-img-wrapper {
    position: relative;
  }

  &__slider-img {
    width: 370px;
    height: 307px;
  }

  &__slider-social {
    position: absolute;
    display: flex;
    justify-content: space-between;
    bottom: 0;
    right: -2px;
    .fa-share-nodes,
    .fa-heart,
    .fa-camera {
      margin: 0 3px 0 0;
      padding: 11px 9px 7px;
      opacity: 0.8;
      border-radius: 2px;
      background-color: #000;
      color: #fff;
    }
  }

  &__slider-name {
    font-size: 24px;
    font-weight: 600;
    color: $text-color;
    margin-top: 19px;
    margin-bottom: 28px;
    margin-left: 30px;
  }

  &__slider-price {
    font-size: 24px;
    font-weight: 700;
    color: #ffc001;
    margin-bottom: 19px;
    margin-left: 30px;
  }

  &__slider-time {
    display: flex;
    justify-content: start;
    margin-bottom: 29px;
    margin-left: 30px;
  }

  &__slider-time-text {
    font-size: 16px;
    color: $text-color;
  }

  &__slider-text {
    width: 185px;
    font-size: 15px;
    color: #616161;
    margin-left: 30px;
  }

  &__slider-more {
    padding: 18px 67px 19px 28px;
    width: 100%;
    font-size: 16px;
    font-weight: 600;
    color: #ffc001;
    box-shadow: 0px 10px 22.5px 2.5px rgba(216, 218, 218, 0.24);
    margin-top: 22px;
    text-transform: uppercase;
    &:hover {
      padding-left: 62px;
      color: #fff;
      background-color: #ffc001;
    }
  }
}

.fa-clock {
  font-size: 16px;
  color: $text-color;
  margin-right: 15px;
}

.holiday {
  background-image: url(../img/holiday-bg.webp);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  height: 700px;
  padding-top: 151px;

  &__wrapper {
    display: flex;
    justify-content: space-between;
  }

  &__content-discont {
    font-size: 16px;
    font-weight: 600;
    color: #ffc001;
    text-transform: uppercase;
    margin-bottom: 29px;
  }

  &__content-percent {
    font-size: 40px;
    font-weight: 700;
    color: $text-color;
    position: relative;
    &::after {
      content: "%";
      position: absolute;
      right: -3px;
      font-size: 16px;
      font-weight: 700;
      color: $text-color;
    }
  }

  &__content-title {
    font-size: 38px;
    margin-bottom: 39px;
  }

  &__content-text {
    font-size: 15px;
    line-height: 2;
    color: #616161;
    max-width: 427px;
    width: 100%;
    margin-bottom: 50px;
  }

  &__image-img {
    width: 600px;
    height: 398px;
  }

  &__content-btn {
    width: 140px;
    height: 40px;
    padding: 14px 27px;
    border-radius: 3px;
    background-color: #ffc001;
    text-transform: uppercase;
    cursor: pointer;
    font-size: 14px;
    font-weight: 700;
    color: #fefefe;
  }
}

.discount {
  background-color: #f9f9f9;
  padding-top: 119px;
  padding-bottom: 232px;
  position: relative;
  &__title {
    text-align: center;
    margin-bottom: 35px;
  }

  &__text {
    max-width: 725px;
    font-size: 15px;
    font-weight: 300;
    line-height: 2;
    text-align: center;
    color: #616161;
    margin-bottom: 57px;
  }

  &__btn-block {
    text-align: center;
  }

  &__btn {
    width: 171px;
    height: 45px;
    padding: 14px 30px;
    margin-bottom: 70px;
    border-radius: 22.5px;
    border: solid 1px #fbad03;
    font-size: 14px;
    font-weight: 700;
    color: #fbad03;
    text-transform: uppercase;
    cursor: pointer;
    &:hover {
      background-color: #ffc001;
      color: #fff;
      transition: all 0.3s ease;
    }
  }

  &__slider {
    position: absolute; /* ← Обязательно */
    bottom: -390px; /* ← Опустить на 80px за пределы section */
    left: 50%; /* ← Центрируем по горизонтали */
    transform: translateX(-50%); /* ← Центрирование */
    width: 90%; /* Ваша ширина */
    max-width: 1200px; /* Ваша максимальная ширина */
    z-index: 10;
  }

  &__item {
    max-width: 770px;
    width: 100%;
    display: flex;
    justify-content: space-between;
    box-shadow: 0 0 30px 0 rgba(230, 230, 230, 0.3);
    background-color: #fff;
    padding-right: 30px;
    margin: 0 auto;
  }

  &__slide-img {
    width: 350px;
    height: 300px;
  }

  &__slide-title {
    font-size: 24px;
    font-weight: 600;
    margin-top: 47px;
    margin-bottom: 40px;
  }

  &__slide-list {
    &:nth-child(2) {
      margin-bottom: 44px;
    }
  }

  &__list-item {
    font-size: 16px;
    margin-bottom: 30px;
    position: relative;
    &::before {
      content: "";
      position: absolute;
      background-image: url(../img/check-box.png);
      background-repeat: no-repeat;
      background-size: cover;
      background-position: center;
      width: 12px;
      height: 12px;
      left: -21px;
      bottom: calc(50% - 6px);
    }
  }

  &__slide-price {
    opacity: 0.8;
    font-size: 40px;
    font-weight: 600;
    color: rgba(251, 173, 3, 0.8);
    margin-left: -21px;
  }

  &__slide-btn {
    width: 45px;
    height: 171px;
    padding: 34px 16px 35px 17px;
    margin-top: 65px;
    opacity: 0.8;
    background-color: #fbad03;
    border: none;
    text-transform: uppercase;
    cursor: pointer;
    writing-mode: vertical-rl;
    text-orientation: mixed;
    transform: rotate(180deg); // Ориентация букв
    font-weight: 700;
    color: #fff;
    // Дополнительно:
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 20px 15px;

    .fa-up-long {
      writing-mode: vertical-rl;
      text-orientation: mixed;
      transform: rotate(180deg);
      margin-bottom: 10px;
    }
  }

  .slick-prev.slick-arrow {
    border: none;
    font-size: 0;
    position: absolute;
    width: 46px;
    height: 46px;
    opacity: 1;
    box-shadow: 0 0 30px 0 rgba(230, 230, 230, 0.3);
    background-color: #fbad03;
    transform: rotate(45deg);
    border-radius: 25% 79% 25% 0;
    left: 0;
    top: 119px;
    z-index: 10;

    &::before {
      content: "";
      background-image: url(../img/angle-left-solid-full.svg);
      position: absolute;
      background-repeat: no-repeat;
      background-position: center;
      background-size: cover;
      display: block;
      width: 20px;
      height: 25px;
      transform: rotate(-45deg);
      left: 13px;
      top: 10px;
    }
  }

  .slick-next.slick-arrow {
    border: none;
    font-size: 0;
    position: absolute;
    width: 46px;
    height: 46px;
    opacity: 1;
    box-shadow: 0 0 30px 0 rgba(230, 230, 230, 0.3);
    background-color: #fbad03;
    border-radius: 25% 79% 25% 0;
    transform: rotate(225deg);
    right: 0;
    top: 119px;
    z-index: 10;

    &::before {
      content: "";
      background-image: url(../img/angle-left-solid-full.svg);
      position: absolute;
      background-repeat: no-repeat;
      background-position: center;
      background-size: cover;
      display: block;
      width: 20px;
      height: 25px;
      transform: rotate(-45deg);
      left: 13px;
      top: 10px;
    }
  }
  .slick-dots {
    margin-top: 30px;
  }
}

.subscribe {
  padding-top: 511px;
  text-align: center;
  position: relative;
  margin-bottom: 341px;

  &__item {
    max-width: 1170px;
    width: 100%;
    height: 458px;
    background-color: #ffc001;
    background-image: url(../img/subscribe-bg.webp);
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    padding-top: 79px;
  }

  &__title {
    font-size: 30px;
    font-weight: 700;
    color: #fff;
    text-transform: uppercase;
    margin-bottom: 39px;
  }

  &__subtitle {
    font-size: 16px;
    letter-spacing: 5.6px;
    color: #fff;
    text-transform: uppercase;
    margin-bottom: 29px;
  }

  &__label {
    max-width: 480px;
    width: 100%;
    position: relative;
  }

  &__input {
    max-width: 480px;
    width: 100%;
    outline: none;
    border: none;
    padding: 16px 100px 16px 31px;
    border-radius: 3px;
    color: $text-color;
    &::placeholder {
      opacity: 0.4;
      font-size: 16px;
      color: rgba(27, 39, 57, 0.4);
    }
  }

  &__btn {
    width: 100px;
    height: 48px;
    padding: 16px 23px;
    background-color: #1b2739;
    font-size: 14px;
    letter-spacing: 0.7px;
    color: #fff;
    border-top-right-radius: 3px;
    border-bottom-right-radius: 3px;
    border: none;
    outline: none;
    position: absolute;
    right: 0;
    cursor: pointer;
    text-transform: uppercase;
  }

  &__video {
    width: 100%; // или своё фиксированное значение
    max-width: 630px;
    height: 330px;
    position: absolute;
    left: calc(50% - 315px);
    bottom: -178px;

    .lty-playbtn {
      position: relative;
      display: inline-flex;
      white-space: nowrap;
      right: 289px;
      top: 273px;
      &::before {
        content: "";
        position: absolute;
        background-image: url(../img/circle-play-solid-full.svg);
        background-repeat: no-repeat;
        background-position: center;
        background-size: cover;
        width: 26px;
        height: 26px;
      }
      &::after {
        content: "Watching video";
        position: absolute;
        font-family: OpenSans;
        font-size: 16px;
        font-weight: 600;
        color: #fff;
        text-transform: uppercase;
        left: 36px;
        top: 5px;
      }
    }
  }
}

.comments {
  margin-bottom: 120px;

  &__items {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    margin-bottom: 120px;
  }

  &__item {
    max-width: 371px;
    width: 100%;
    box-shadow: 0 0 30px 0 rgba(230, 230, 230, 0.25);
    background-color: #fff;
  }

  &__img {
    max-width: 371px;
    width: 100%;
    margin-bottom: 27px;
  }

  &__content {
    padding-left: 30px;
    padding-right: 29px;
    padding-bottom: 34px;
  }

  &__data {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    font-size: 12px;
    font-weight: 600;
    color: #b9bbba;
    text-transform: uppercase;
    margin-bottom: 29px;
  }

  &__title {
    font-size: 20px;
    line-height: 1.8;
    color: $text-color;
  }

  &__text {
    font-size: 15px;
    font-weight: 300;
    line-height: 1.87;
    letter-spacing: 0.23px;
    color: #616161;
    margin-bottom: 30px;
  }

  &__list {
    display: flex;
    justify-content: start;
  }

  &__links {
    width: 40px;
    height: 40px;
    margin-right: 30px;
    opacity: 0.5;
    background-color: #f3f0f0;
    text-align: center;
    cursor: pointer;

    &:nth-child(1) {
      border-bottom-left-radius: 20%;
      border-bottom-right-radius: 60%;
      border-top-right-radius: 20%;
    }

    &:nth-child(2) {
      border-bottom-left-radius: 40%;
      border-bottom-right-radius: 40%;
      border-top-right-radius: 10%;
      border-top-left-radius: 10%;
    }

    &:nth-child(3) {
      border-top-left-radius: 20%;
      border-bottom-right-radius: 20%;
      border-bottom-left-radius: 60%;
    }

    a {
      margin-top: 13px;
    }

    .fa-comment {
      color: #fbad03;
    }

    .fa-heart {
      color: #28c190;
    }

    .fa-share-nodes {
      color: #1b2739;
    }
  }
}

.footer {
  background-image: linear-gradient(to top, #32354b, #2b2e42);
  position: relative;
  padding-top: 119px;
  &::before {
    content: "";
    position: absolute;
    background-image: url(../img/footer-bg.png);
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    width: 1280px;
    height: 641px;
    top: 16px;
    left: 199px;
    z-index: 0;
  }

  &__top {
    position: relative;
    z-index: 1;
    padding-bottom: 146px;
    border-bottom: 1px solid rgba(255, 255, 255, 0.15);
  }

  &__top-items {
    display: flex;
    justify-content: space-between;
  }

  &__logo {
    margin-bottom: 45px;

    img {
      width: 168px;
      height: 59px;
    }
  }

  &__contacts-links {
    margin-bottom: 26px;

    &:nth-child(3) {
      margin-bottom: 51px;
    }

    a {
      font-size: 14px;
      letter-spacing: 0.21px;
      color: #bababa;
      &:hover {
        color: #ffc001;
      }
    }
  }

  &__top-list {
    display: flex;
    justify-content: start;
  }

  &__top-links {
    margin-right: 31px;
    font-size: 14px;
  }

  &__support,
  &__location,
  &__menu {
    font-size: 16px;
    font-weight: 600;
    color: #fff;
    text-transform: uppercase;
    margin-bottom: 14px;
  }

  &__devider {
    width: 24px;
    height: 1px;
    background-color: #ffc001;
    margin-bottom: 40px;
  }

  &__support-links,
  &__location-links,
  &__menu-links {
    margin-bottom: 30px;

    &:nth-child(5) {
      margin-bottom: 0;
    }

    a {
      font-size: 14px;
      color: #bababa;
      &:hover {
        color: #ffc001;
      }
    }
  }

  &__bottom {
    display: flex;
    justify-content: space-between;
    padding-top: 49px;
    padding-bottom: 92px;
    position: relative;
    z-index: 1;
  }

  &__bottom-copyriting {
    font-size: 14px;
    color: rgba(255, 255, 255, 0.2);
  }

  &__bottom-list {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
  }

  &__bottom-link {
    margin-left: 50px;
    a {
      font-size: 14px;
      color: rgba(255, 255, 255, 0.2);
      &:hover {
        color: #ffc001;
      }
    }
  }
}

@import "components/media";
