@charset "UTF-8";
*,
*::before,
*::after {
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
  font-size: 16px;
  scroll-behavior: smooth;
}

html,
body {
  height: 100%;
  position: relative;
  overflow-x: hidden;
}

body {
  font-family: "OpenSans", sans-serif;
  font-style: normal;
  font-weight: 400;
  color: #333333;
  margin: 0;
}

a {
  display: inline-block;
  text-decoration: none;
}

button {
  text-decoration: none;
}

ul,
li {
  margin: 0;
  padding: 0;
  list-style: none;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0 auto;
}

.wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100%;
}

.content {
  flex: 1 0 auto;
}

.footer {
  flex: 0 0 auto;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 15px;
}

.booking-widget {
  position: absolute;
  bottom: -189px;
}

.booking-widget__tabs {
  display: flex;
  justify-content: start;
}

.booking-widget__tabs-stripe {
  width: 24px;
  height: 45px;
  background-color: #ffc001;
}

.booking-tab {
  width: 100px;
  height: 45px;
  padding: 17px 0 16px 0;
  background-color: #fff;
  border: none;
  margin-right: 1px;
  text-transform: uppercase;
  font-size: 14px;
  color: #333333;
  margin-bottom: 2px;
  cursor: pointer;
}
.booking-tab--active {
  background-color: #ffc001;
  color: #fff;
  font-weight: 600;
}

.booking-widget__content {
  position: relative;
  background: #fff;
  max-width: 1170px;
}

.booking-form {
  display: none;
  justify-content: start;
  background-color: #fafafa;
}
.booking-form--active {
  display: flex;
}

.booking-form__form-stripe {
  width: 24px;
  height: 187px;
  background-color: #ffc001;
  margin-right: 45px;
}

.booking-form__group {
  display: flex;
  flex-direction: column;
  margin-right: 80px;
}
.booking-form__group:nth-child(5) {
  margin-right: 59px;
}

.booking-form__label {
  font-size: 14px;
  color: #616161;
  text-transform: uppercase;
  margin-top: 55px;
  margin-left: 0;
  margin-bottom: 14px;
}

.booking-form__input {
  width: 190px;
  height: 50px;
  border: none;
  padding-left: 30px;
  outline: none;
  font-size: 14px;
  color: #616161;
}
.booking-form__input::placeholder {
  opacity: 0.35;
  font-size: 14px;
  color: rgba(37, 31, 31, 0.35);
}

.booking-form__submit {
  width: 40px;
  height: 109px;
  margin: 39px 0;
  padding: 22px 15px;
  background-color: #ffc001;
  text-transform: uppercase;
  font-size: 14px;
  font-weight: 600;
  color: #fdfdfd;
  border: none;
  cursor: pointer;
  writing-mode: vertical-rl;
  text-orientation: mixed;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 20px 15px;
}

.stars {
  display: inline-block;
  font-size: 20px;
  color: #ddd;
  position: relative;
  margin-left: 30px;
}

.stars::before {
  content: "★★★★★";
  position: absolute;
  top: 0;
  left: 0;
  width: calc(var(--rating) * 20%);
  color: #ffc001;
  overflow: hidden;
}

.countdown {
  display: flex;
  justify-content: center;
  gap: 140px;
}

.countdown__item {
  text-align: left;
}
.countdown__item:nth-child(1) {
  color: #fbad03;
}
.countdown__item:nth-child(2) {
  color: #ef6f6c;
}
.countdown__item:nth-child(3) {
  color: #28c190;
}
.countdown__item:nth-child(4) {
  color: #333333;
}

.countdown__number-days,
.countdown__number-hours,
.countdown__number-minutes,
.countdown__number-seconds {
  font-family: "Roboto", sans-serif;
  font-weight: 700;
  font-size: 36px;
  letter-spacing: 1.8px;
}

.countdown__label {
  font-family: "Roboto", sans-serif;
  font-weight: 500;
  font-size: 16px;
  letter-spacing: 0.24px;
  text-transform: uppercase;
}

.header {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 100;
  background: transparent;
}
.header__top {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  border-bottom: 1px solid #fd0;
  align-items: center;
  position: relative;
  padding: 14px 0;
}
.header__social {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.header__social-list {
  margin-right: 30px;
}
.header__top-right {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}
.header__top-right .header__nav {
  display: none;
}
.header__location {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  align-items: center;
}
.header__registration {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  align-items: center;
  margin-left: 64px;
}
.header__location-adress {
  font-size: 12px;
  letter-spacing: 0.18px;
  color: #fff;
  margin-left: 15px;
}
.header__registration-login, .header__registration-register {
  text-transform: uppercase;
  font-size: 12px;
  font-weight: 600;
  color: #fff;
}
.header__registration-login:hover, .header__registration-register:hover {
  color: #ffc001;
  transition: all 0.3s ease;
}
.header__devider {
  width: 1px;
  height: 20px;
  background-color: #fd0;
  margin: 0 12px;
}
.header__bottom {
  max-height: 75px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding-top: 16px;
  position: relative;
}
.header__nav-menu {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}
.header__nav-list {
  margin: 0 25px;
}
.header__nav-list:nth-child(1) {
  margin-left: 0;
}
.header__nav-list:nth-child(5) {
  margin-right: 0;
}
.header__nav-link {
  text-transform: uppercase;
  font-size: 14px;
  color: #fff;
}
.header__nav-link:hover {
  color: #ffc001;
  transition: all 0.3s ease;
}

.fa-brands,
.fa-location-dot {
  color: #fff;
}
.fa-brands:hover,
.fa-location-dot:hover {
  color: #ffc001;
  transition: all 0.3s ease;
}

.hero {
  position: relative;
  min-height: 100vh;
  background-color: #000;
  padding-top: 375px;
  margin-bottom: 308px;
}
.hero::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: url(../img/background-hero.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  opacity: 0.7;
  z-index: 0;
}
.hero__tel {
  position: relative;
  z-index: 1;
  margin-bottom: 40px;
  font-size: 20px;
  font-weight: 700;
  letter-spacing: 0.3px;
  color: #ffc001;
}
.hero__title {
  position: relative;
  z-index: 1;
  margin-bottom: 30px;
  font-size: 48px;
  font-weight: 700;
  color: #fff;
}
.hero__text {
  position: relative;
  z-index: 1;
  margin: 0 0 50px 0;
  max-width: 590px;
  font-size: 16px;
  text-align: left;
  color: #fff;
}
.hero__btn-block {
  position: relative;
  z-index: 1;
  margin-bottom: 100px;
}
.hero__btn {
  width: 171px;
  height: 45px;
  padding: 14px 20px;
  border-radius: 22.5px;
  border: solid 1px #fff;
  background-color: transparent;
  font-size: 14px;
  font-weight: 700;
  text-transform: uppercase;
  cursor: pointer;
  text-align: center;
  align-items: center;
  color: #ffc001;
}
.hero__btn:hover {
  background-color: #ffc001;
  color: #fff;
  border: solid 1px #ffc001;
  transition: all 0.3s ease;
}

.section-title {
  font-size: 30px;
  font-weight: 700;
  color: #333333;
}

.section-text {
  font-size: 16px;
  color: #333333;
}

.slick-initialized .slick-slide {
  display: flex;
  justify-content: center;
}

.slick-dots {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 16px;
}

.places {
  margin-bottom: 210px;
}
.places__subtitle {
  font-size: 14px;
  font-weight: 600;
  color: #ffc001;
