}
.places__big-slider .slick-arrow {
  font-size: 0;
  overflow: hidden;
  white-space: nowrap;
  color: transparent;
  position: absolute;
  bottom: calc(700px - 50%);
}
.places__big-slider .slick-dots {
  position: relative;
  margin-top: 30px;
  z-index: 2;
}
.places__big-slider {
  /* убираем псевдо-элемент, который рисует надпись */
}
.places__big-slider .slick-arrow::before {
  content: none;
}
.places__big-slider .slick-prev {
  border: none;
  background-color: transparent;
  left: 358px;
  z-index: 10;
  width: 28px;
  height: 28px;
}
.places__big-slider .slick-prev:before {
  content: "";
  display: block;
  width: 17px;
  height: 28px;
  background-image: url(../img/chevron-left-solid-full.svg);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  opacity: 1;
}
.places__big-slider .slick-next {
  border: none;
  background-color: transparent;
  right: 358px;
  z-index: 10;
  width: 28px;
  height: 28px;
}
.places__big-slider .slick-next:before {
  content: "";
  display: block;
  width: 17px;
  height: 28px;
  background-image: url(../img/chevron-right-solid-full.svg);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  opacity: 1;
}
.places__big-slider .slick-dots {
  bottom: 23px;
}
.places__big-slider-item {
  position: relative;
}
.places__big-slider-img-wrapper {
  padding-bottom: 110px;
}
.places .slick-list .draggable {
  margin-bottom: -71px;
}

.slider__info {
