.booking-widget {
  position: absolute;
  bottom: -189px;
}

.booking-widget__tabs {
  display: flex;
  justify-content: start;
}

.booking-widget__tabs-stripe {
  width: 24px;
  height: 45px;
  background-color: #ffc001;
}

.booking-tab {
  width: 100px;
  height: 45px;
  padding: 17px 0 16px 0;
  background-color: #fff;
  border: none;
  margin-right: 1px;
  text-transform: uppercase;
  font-size: 14px;
  color: $text-color;
  margin-bottom: 2px;
  cursor: pointer;
  &--active {
    background-color: #ffc001;
    color: #fff;
    font-weight: 600;
  }
}

.booking-widget__content {
  position: relative;
  background: #fff;
  max-width: 1170px;
}

.booking-form {
  display: none;
  justify-content: start;
  background-color: #fafafa;
  &--active {
    display: flex; // Показываем активную (или display: block, смотря что у вас)
  }
}

.booking-form__form-stripe {
  width: 24px;
  height: 187px;
  background-color: #ffc001;
  margin-right: 45px;
}

.booking-form__group {
  display: flex;
  flex-direction: column;
  margin-right: 80px;
  &:nth-child(5) {
    margin-right: 59px;
  }
}

.booking-form__label {
  font-size: 14px;
  color: #616161;
  text-transform: uppercase;
  margin-top: 55px;
  margin-left: 0;
  margin-bottom: 14px;
}

.booking-form__input {
  width: 190px;
  height: 50px;
  border: none;
  padding-left: 30px;
  outline: none;
  font-size: 14px;
  color: #616161;
  &::placeholder {
    opacity: 0.35;
    font-size: 14px;
    color: rgba(37, 31, 31, 0.35);
  }
}

.booking-form__submit {
  width: 40px;
  height: 109px;
  margin: 39px 0;
  padding: 22px 15px;
  background-color: #ffc001;
  text-transform: uppercase;
  font-size: 14px;
  font-weight: 600;
  color: #fdfdfd;
  border: none;
  cursor: pointer;
  writing-mode: vertical-rl; // Текст вертикально (справа налево)
  text-orientation: mixed; // Ориентация букв

  // Дополнительно:
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 20px 15px;
}
