.countdown {
  display: flex;
  justify-content: center;
  gap: 140px;
}

.countdown__item {
  text-align: left;
  &:nth-child(1) {
    color: #fbad03;
  }

  &:nth-child(2) {
    color: #ef6f6c;
  }

  &:nth-child(3) {
    color: #28c190;
  }

  &:nth-child(4) {
    color: $text-color;
  }
}

.countdown__number-days,
.countdown__number-hours,
.countdown__number-minutes,
.countdown__number-seconds {
  font-family: "Roboto", sans-serif;
  font-weight: 700;
  font-size: 36px;
  letter-spacing: 1.8px;
}

.countdown__label {
  font-family: "Roboto", sans-serif;
  font-weight: 500;
  font-size: 16px;
  letter-spacing: 0.24px;
  text-transform: uppercase;
}
